  height: 24px;
  border-width: 2px;
  position: absolute;
  left: 0;
  top: 0;
}
.loading.mini .loading-indicator.relative {
  position: relative;
  left: auto;
  top: auto;
}
.loading.mini .loading-message {
  padding-left: 30px;
  margin-top: 1px;
  display: inline-block;
}
.loading.triangle {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 500px;
  margin-top: -200px;
  margin-left: -250px;
}
