/**
* Dropdowns
* ============================================================================
*/
.dropdown-menu {
  top: 32px;
  border: none;
  border-radius: 2px;
  box-shadow: 0 0 0 1px rgba(52, 60, 69, 0.2), 0 1px 3px rgba(70, 82, 98, 0.25);
  -webkit-background-clip: padding-box;
  -moz-background-clip: padding;
  background-clip: padding-box;
  color: #2f2936;
}
.dropdown-menu.inverted {
  top: auto;
  bottom: 32px;
}
.dropdown-menu:after {
  width: 0;
  height: 0;
  border-left: 8px solid transparent;
  border-right: 8px solid transparent;
  border-bottom: 8px solid #fff;
  content: '';
  display: block;
  position: absolute;
  top: -8px;
  left: 12px;
  z-index: -1;
}
.dropdown-menu.inverted:after {
  border-top: 8px solid #fff;
  border-bottom: 8px solid transparent;
  top: auto;
  bottom: -16px;
}
.dropdown-menu:before {
  width: 0;
  height: 0;
  border-left: 9px solid transparent;
  border-right: 9px solid transparent;
  border-bottom: 9px solid rgba(52, 60, 69, 0.35);
  content: '';
  display: block;
  position: absolute;
  top: -9px;
  left: 11px;
  z-index: -2;
}
.dropdown-menu.inverted:before {
  border-top: 9px solid rgba(52, 60, 69, 0.35);
  border-bottom: 9px solid transparent;
  top: auto;
  bottom: -18px;
}
.dropdown-menu.dropdown-menu-right:before {
  right: 12px;
  left: auto;
}
.dropdown-menu.dropdown-menu-right:after {
  right: 13px;
  left: auto;
}
.dropdown-menu li.active a,
.dropdown-menu li.active a:hover {
  background: #6c5fc7;
  color: #fff !important;
}
.dropdown-menu .disabled {
  opacity: 0.4;
  cursor: default;
}
.dropdown-menu .disabled:hover {
  background: #fff;
  color: #7a8188;
}
.anchor-right .dropdown-menu {
  left: auto;
  right: 0;
}
.anchor-right .dropdown-menu:after {
  left: auto;
  right: 12px;
}
.anchor-right .dropdown-menu:before {
  left: auto;
  right: 11px;
}
.anchor-middle .dropdown-menu {
  left: auto;
  right: 50%;
  transform: translateX(calc(50%));
}
.anchor-middle .dropdown-menu:after {
  left: calc(50% - 5px);
  right: auto;
}
.anchor-middle .dropdown-menu:before {
  left: calc(50% - 6px);
  right: auto;
}
